
<template>
    <section id="main-content" class="main-content">
    <section class="page-action">        
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('BulkUpload')}}</span>
                        <span class="p-actions float-right">
                            <a href="javascript:;" class="p-action-btn text-white" id="PageTour" title="Page Tour" @click="setTourGuideSteps(0)"><i class="fa fa-road" aria-hidden="true"></i><br>Page Tour</a>
                            </span>
                    </div>
                </div>
            </div>       
    </section>
    <div class="user-guide-custom d-none">
        {{$t('User_Guide_ExpenseImport')}}
    </div>   
    <div class="border p-3" v-if="ExpenseDetails">                  
            <div class="row" >
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('DownloadSampleFile')}}:</label>
                        <div class="input-group mb-3">
                            <div class="form-control">
                                <i class="fa fa-file-excel-o pr-1 text-success"></i>{{$t('Expense')}}.xls
                            </div>
                            <div class="input-group-append downloadExpense">
                                <span class="input-group-text" id="basic-addon2">
                                    <a href="/Content/files/Expense.xls" class="" download>
                                        <i class="fa fa-download text-primary"></i>
                                    </a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        {{$t('UploadFile')}}
                        <label>{{$t('UploadFile')}}:<span class="text-danger"></span></label>
                        <a href="javascript:;" data-toggle="popoveruserguide" data-content="Allowed File Type: .xls, .xlsx">
                            <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                        </a>
                        <div class="row">
                            <div class="col-9 col-sm-10 col-md-10 col-lg-10 col-xl-10 mb-2 float-left">
                                <div class="custom-file expenseFile">
                                    <input type="file" name="FileSrc" id="flAvatar" @change="setFile($event)" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                   <a href="javascript:void(0);" @click="UploadExpenseFile()" :title="$t('ImportFromExcelFile')" class="btn btn-info formbtn mr-1"><i class="fa fa-file-excel-o mr-2"></i>{{$t('ImportButton')}}</a>
                   <RouterLink to="/Expenses/Index" title="Cancel" class="btn btn-danger formbtn widthhalf" tabindex="4"><i
                            class="fa fa-close pr-2"></i>{{$t('Cancel')}}</RouterLink>                           
                </div>
            </div>
           
    </div>
    <ImportExpenseDeatils v-if="ExpenseDetails==false && expListing.length>0" :expListing='expListing' :trips='trips' :categories="categories" :projects="projects" :currencies="currencies" :tasks="tasks" :frmFields="frmFields"></ImportExpenseDeatils>
</section>
</template>
<script>
import DataService from '../../services/DataService';
import ImportExpenseDeatils from '../Common/ImportExpenseDetail.vue'
export default {
    
    components: {
        ImportExpenseDeatils        
      },
    data: function () {
        return {
            isLoading: false,
            file: null,
            filepath: null,
            excelFileName: null,
            reqfrom: null,
            ExpenseDetails:true,
            expListing:null,
            trips:[],
            categories:[],
            projects:[],
            currencies:[],
            tasks:[],
            expLimitJsonBack:[],
            check_EXPENSE_LIMIT:false,
            userDateFormat:null,
            files:new FormData() ,
            frmFields:null,
            value:null  
        }
    },
    created: function () {
        //this.reqfrom = this.$route.params.reqfrom;
    },
    methods:{
        setFile: function (e) {
            this.file = e.target.files[0];
            this.excelFileName = e.target.files[0].name;
        },
        UploadExpenseFile() {
            var vm = this;
            if ($("#flAvatar").val() != "") {
                var file = $("#flAvatar").val().split('\\').pop();
                var allowFileTypesExt = '.xls,.xlsx';
                var extension = file.replace(/^.*\./, '');
                if (!allowFileTypesExt.includes(extension)) {
                    vm.ShowAlert(vm.$t('InvalidSampleFileUploadMsg', [file, allowFileTypesExt]), "warning", true, vm.$t('Alert'));
                }
                else {
                    const formdata = new FormData();
                    formdata.append("file", vm.file);
                    DataService.ImportExpensePost(formdata).then(function (response) {
                      // debugger;
                        var data = response.data;
                        if (data.Status == 0) {
                            vm.isLoading = false;
                            // vm.$router.push({name:'ImportExpenseDetail',params:{
                            //     filepath:data.Msg
                            // }}); 
                            vm.UploadExpenseDetails(data.Msg);
                            vm.ExpenseDetails=false
                        }
                        else {
                            vm.isLoading = false;
                            vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                        }
                    });
                }
            }
        },
        UploadExpenseDetails(ths) { 
            debugger;               
            var vm = this;
            var param = 'fullPath=' + ths;
            DataService.UploadExpenseDetails(param).then(function (response) {                
                if (response.data.msgFor == "success") {
                    debugger;
                    var data = response.data;
                    data.listing.forEach(function(x){                        
                        x.tasks = [];
                        if(typeof(x.projectId) == 'undefined' || x.projectId == null || x.projectId == 0){
                            x.projectId = "";
                        }
                        if(typeof(x.tripId) == 'undefined' || x.tripId == null || x.tripId == 0){
                            x.tripId = "";
                        }
                        if(typeof(x.taskId) == 'undefined' || x.taskId == null || x.taskId == 0){
                            x.taskId = "";
                        }
                        if(typeof(x.categoryId) == 'undefined' || x.categoryId == null || x.categoryId == 0){
                            x.categoryId = "";
                        }
                    });
                    vm.expListing = data.listing;
                    vm.trips = data.Trips;
                    vm.projects = data.Projects;
                    vm.trips = data.Trips;
                    vm.categories = data.Categories;
                    vm.projects = data.Projects;
                    vm.currencies = data.Currencies;
                }
                
                setTimeout(function(){
                    var frmFields={}
                        vm.expListing.forEach((item,index) => {
                        frmFields['datepicker'+index]=item.expenseDate;
                        frmFields['tripId'+index]=item.tripId?String(item.tripId):'';
                        frmFields['expenseTitle'+index]=item.expenseTitle;
                        frmFields['categoryId'+index]=item.categoryId?String(item.categoryId):'0';
                        frmFields['projectId'+index]=item.projectId?String(item.projectId):'';
                        frmFields['currencyId'+index]=item.currencyId?String(item.currencyId):'';
                        frmFields['taskId'+index]=item.taskId?String(item.taskId):'';
                        frmFields['expenseAmount'+index]=item.expenseAmount?String(item.expenseAmount):'';
                        frmFields['description'+index]=item.description;
                    })
                   // vm.$refs.form.resetForm({ values: frmFields });
                },1000);     
            });

        },
    }
}
</script>